firebase.auth().onAuthStateChanged(user => {
  store.dispatch("fetchUser", user);
});



firebase.getCurrentUser = () => {
  return new Promise((resolve, reject) => {
    const unsubscribe = firebase.auth().onAuthStateChanged(user => {
      unsubscribe();
      resolve(user);
    }, reject);
  })
};

