import Vue from "vue";
import VueRouter from "vue-router";
import firebase from "../plugins/firebaseInit.js";

Vue.use(VueRouter);

const routes = [
  {
    path: "/login",
    name: "login",
    component: () => import("../components/Auth/Login"),
  },

  {
    path: "/",
    name: "sistema-x",
    component: () => import("../components/SistemaX.vue"),

    meta: {
      requiresAuth: true,
    },

    children: [
      {
        path: "dashboard",
        name: "Dashboard",
        component: () => import("../components/X/Dashboard.vue"),
        meta: {
          requiresAuth: true,
        },
      },
      {
        path: "orders",
        name: "Orders",
        component: () => import("../components/X/Orders.vue"),
        meta: {
          requiresAuth: true,
        },
      },
      {
        path: "customer",
        name: "Customer",
        component: () => import("../components/X/Customers.vue"),
        meta: {
          requiresAuth: true,
        },
      },
      {
        path: "customer/:customerId/orders/",
        name: "CustomerOrders",
        component: () => import("../components/X/CustomerOrders.vue"),
        meta: {
          requiresAuth: true,
        },
      },
      {
        path: "reports",
        name: "Report",
        component: () => import("../components/X/ReportOrders.vue"),
        meta: {
          requiresAuth: true,
        },
      },
    ],
  },
  {
    path: "/about",
    name: "About",
    metaInfo: {
      title: "Home Page - Example App",
    },
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/About.vue"),
  },
];

const router = new VueRouter({
  // mode: "history",
  base: process.env.BASE_URL,
  routes,
});

router.beforeEach(async (to, from, next) => {
  const requiresAuth = to.matched.some((record) => record.meta.requiresAuth);
  if (requiresAuth && !(await firebase.getCurrentUser())) {
    next("login");
  } else {
    next();
  }
});

export default router;
